.contact-page{max-width:800px;margin:0 auto;padding:3rem 1rem;font-family:"Orbitron",sans-serif;color:var(--text)}.contact-page h1{text-align:center;margin-bottom:2rem}.contact-page form{display:flex;flex-direction:column;gap:1rem}.contact-page form input,.contact-page form textarea{padding:.75rem;border:1px solid var(--card-border);border-radius:8px;background:var(--card-bg);color:var(--text)}.contact-page form .form-error{color:red;font-weight:500}.contact-page form .form-success{color:green;font-weight:500}.contact-page form button{background:var(--accent);color:#000;padding:.75rem;border-radius:8px;font-weight:bold;cursor:pointer}.contact-page form button:hover{filter:brightness(1.1)}